<template>
	<div class="empresa-info" >
		<div class="empresa-imagecard">
			<div class="container" >
				<img v-if="getEmpresa.url_image" :src="getEmpresa.getImageURL()"> 
				<img  v-else src="@/assets/empresa_default.png" class="image"> 
				<div class="overlay">
					<va-button size="small" color="verde" text-color="cinza" class="mb-3" @click="showImg(getEmpresa.getImageURL())">Abrir</va-button>
					<va-file-upload v-model="file" color="verde" type="single" @input="input" file-types=".jpg,.png,.svg,.eps,.PNG,.jpeg"/>
				</div>
			</div>
			<va-modal v-model="show_upload" :mobile-fullscreen="false" color="verde"  @ok="enviar" ok-text="Enviar" cancel-text="Cancelar">
			</va-modal>
			<div style="display:flex; flex-direction: column">
				<va-input v-model="empresa.company_name" :style="'margin: 10px'" color="cinza" label="RAZÃO SOCIAL" type="name"/>
				<va-input v-model="empresa.CNPJ" :style="'margin: 10px; color: red'" color="cinza" label="CNPJ" placeholder="XX.XXX.XXX/0001-XX"/>
				<div style="margin-left: 20px; margin-top: 20px;"><va-button color="verde" text-color="cinza" @click="edit()" >Atualizar</va-button></div>
			</div>
		</div>

		<div class="empresa-work-shift">
			<div class="empresa-work-shift-title">Turno de Trabalho 
				<va-button flat color="verde" text-color="verde" v-if="empresa.work_shift == null" @click="add()" icon="add" ></va-button>
			</div>
			<div v-for="day in empresa.work_shift" :key="day.day_of_week" >
				<div class="empresa-work-shift-day">
					<va-switch v-model="day.select" :label="day.day" size="small" color="verde" @click="edit"/>
					<va-button icon="add" flat color="verde" size="small" @click="show[day.day_of_week]=true"></va-button>
					<!--<va-button icon="edit" flat color="verde" @click="show = true"></va-button>-->
				</div>
				<div v-for="turno in day.list_hours" :key="turno">
					<div style="display: flex; flex-direction: row;" v-if="day.select == true">
						<va-input v-model="turno.start_hour" color="cinza" label="Inicio" v-if="turno.start_hour != null"/>
						<va-input v-model="turno.end_hour" color="cinza" label="Fim" v-if="turno.start_hour != null"/>
						<va-button icon="close" flat color="cinza" size="small" v-if="turno.start_hour != null" @click="deleteTurno(day.day, turno)" ></va-button>
					</div>
				</div>
				<div style="display: flex; flex-direction: row" v-if="day.select == true && show[day.day_of_week] == true" >
					<va-input v-model="valueInicio" color="cinza" label="Inicio"/>
					<va-input v-model="valueFim" color="cinza" label="Fim"/>
					<va-button color="verde" text-color="cinza" @click="addTurno(day.day, day.day_of_week)">Adicionar </va-button>
					<va-button color="laranja" text-color="cinza" @click="cancel(day.day_of_week)">Cancelar </va-button>
				</div>
			</div>
		</div>
	</div>
	
</template>

<script>
//import {url} from '../../configs/global'
import Usuario from '../../models/usuario'
export default {
	name: 'EmpresaInfo',
	data: () => ({
		empresa: {},
		file: [],
		show_upload: false, 
		show : [false, false, false, false, false, false, false],
		valueInicio: null,
		valueFim: null,
    }),
    computed: {
        getEmpresa(){
			return this.$store.getters['empresa.get']
		}
    },
	watch: {
		getEmpresa(value){
			this.empresa = value
		}
	},
	methods:{
		load(){
			this.empresa = this.getEmpresa
		},
		add(){
			this.empresa.work_shift = { "Segunda-Feira": { "day_of_week": "2", "day": "Segunda-Feira", "select": false, "list_hours": [] }, "Terça-Feira": { "day_of_week": "3", "day": "Terça-Feira", "select": false, "list_hours": [] }, "Quarta-Feira": { "day_of_week": "4", "day": "Quarta-Feira", "select": false, "list_hours": [] }, "Quinta-Feira": { "day_of_week": "5", "day": "Quinta-Feira", "select": false, "list_hours": [] }, "Sexta-Feira": { "day_of_week": "6", "day": "Sexta-Feira", "select": false, "list_hours": [] }, "Sábado": { "day_of_week": "7", "day": "Sábado", "select": false, "list_hours": [] }, "Domingo": { "day_of_week": "1", "day": "Domingo", "select": false, "list_hours": []} }
			this.edit()
		},
		addTurno(day, day_status){
			this.show[day_status] = false;
			let list_hours = [] 
			for (let i = 0; i < this.empresa.work_shift[day].list_hours.length; i++) {
				let start = this.empresa.work_shift[day].list_hours[i].start_hour
				let end = this.empresa.work_shift[day].list_hours[i].end_hour
				if(start != null && end !== null) list_hours = list_hours.concat({"start_hour": start, "end_hour": end })
				}
			list_hours = list_hours.concat({"start_hour": this.valueInicio, "end_hour": this.valueFim })
			this.empresa.work_shift[day].list_hours = list_hours
			this.edit()
			this.valueInicio = null
			this.valueFim = null
		},
		deleteTurno(day,turno){
			let list_hours = [] 
			for (let i = 0; i < this.empresa.work_shift[day].list_hours.length; i++) {
				if(turno != this.empresa.work_shift[day].list_hours[i]){
					let start = this.empresa.work_shift[day].list_hours[i].start_hour
					let end = this.empresa.work_shift[day].list_hours[i].end_hour
					list_hours = list_hours.concat({"start_hour": start, "end_hour": end })
				}
			}
			this.empresa.work_shift[day].list_hours = list_hours
			this.edit()
		},
		cancel( day_status){
			this.show[day_status] = false;
		},
		edit(){
			this.$store.dispatch('loading.show')
			this.empresa.update({
				getters: this.$store.getters
			}).then(async () => {
				this.$store.dispatch('toast.success', 'Atualizado!')
			}).catch(function (error) {
				console.log(error)
				this.$store.dispatch('toast.error', 'Erro!')
			}).finally(()=>this.$store.dispatch('loading.hide'))
		},
		showImg(img){
			window.open(img, "_blank");	
		},
		input: function(){
			this.show_upload = true
		},
		enviar: function() {
			this.empresa.savePhoto({
				getters: this.$store.getters,
				blob: this.file[0]
			}).then(async () => {
				this.file = []
				this.$store.dispatch('toast.success', 'Atualizado!')
				this.$store.dispatch('empresa.set', await Usuario.getAllCompanies(this.axios));
			}).catch((err)=>{
				console.log(err, err.data)
				this.$store.dispatch('toast.error', 'Erro!');
			})
		},
	},
	mounted() {
		this.load()
	},
}
</script>

<style scoped>

.empresa-info {
	margin-bottom: 10px;
	margin-top: 10px;
	display: flex;
	flex-direction: row;
}

.empresa-imagecard {
	background-color: var(--c4);
	padding: 10px;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
	width: 50%;
	text-align: left;
}

.empresa-work-shift {
	margin-left: 10px;
	background-color: var(--c4);
	padding: 10px;
	display: flex;
	flex-direction: column;
	/*justify-content: space-between;*/
	color: var(--c3);
	width: 50%;
	height: 100%;
}

.empresa-work-shift-day {
	display:flex; 
	flex-direction: row; 
	margin-bottom: 1px;
	align-items: center ;
	justify-content: space-between;
}

.empresa-work-shift-title {
	font-size: 1.5em;
	padding: 15px;
	color: var(--branco);
	/*font-weight: bold;*/
}

.container{
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 5px;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background-color: #3C3F4A99;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.container:hover .overlay {
  opacity: 1;
}
.image{
	width: 250px;
	height: 250px;
}
</style>