<template>
  <FloatButton @click="refresh" icon="refresh" type="vuestic" />
  <FloatButton v-if="aba.model" @click="add" icon="add" type="vuestic" style="bottom: 90px" />
  <FloatButton v-if="aba.tipo == 'servicos'" @click="openCalendar" icon="calendar_month" type="vuestic" style="bottom: 160px" />
  <FloatButton v-if="aba.tipo == 'planos' || aba.tipo == 'checklists'" @click="showAll = !showAll" :icon="showAll? 'visibility_off' : 'visibility'" type="vuestic" style="margin-bottom: 140px;"/>
  <div class="system-patrimonio">
    <Estatistica
      v-if="aba.tipo == 'ativos'"
      :objetos="getObjetos"
      :aba="aba"
      img="ativos.svg"
      caption='"Se você pode tocar, cadastre aqui."'
    />
    <!--<Estatistica
      v-if="aba.tipo == 'reports'"
      :objetos="getObjetos"
      :aba="aba"
      img="reports.svg"
      caption='"Se você não pode tocar, cadastre aqui."'
    />
    <Estatistica
      v-if="aba.tipo == 'servicos'"
      :objetos="getObjetos"
      :aba="aba"
      img=""
      caption=""
    />-->
    <TabelaGeral :objetos="getObjetos" :aba="aba" />
    <!--<div v-for="objeto in getObjetos" :key="objeto.id">
			<div style="padding: 10px;">{{objeto}}</div>
		</div>-->
  </div>
  <!--<ModalCalendar :objetos="getObjetos" :aba="aba"> </ModalCalendar>-->
  <!--<ModalServicoConfig :aba="aba" />-->
  <ModalDocumentos />
</template>

<script>
import FloatButton from "@/utils/FloatButton";
import Estatistica from "@/utils/Estatistica";
import TabelaGeral from "@/utils/TabelaGeral";
import ModalDocumentos from '../components/Patrimonio/ModalDocumentos'
//import ModalCalendar from "@/components/Patrimonio/ModalCalendar";
//import ModalServicoConfig from "@/components/Patrimonio/ModalServicoConfig";
export default {
  name: "SystemPatrimonio",
  components: {
    FloatButton,
    Estatistica,
    TabelaGeral,
    //ModalCalendar,
    //ModalServicoConfig,
    ModalDocumentos,
  },
  data: () => ({
    open: false,
    showAll: false
  }),
  computed: {
    getObjetos() {
      console.log(this.aba)
      if (this.aba.tipo == "ativos") return this.$store.getters["ativos.get"];
      if (this.aba.tipo == "reports") return this.$store.getters["reports.get"];
      if (this.aba.tipo == "servicos") return this.$store.getters["servicos.get"];
      if (this.aba.tipo == "planos") return this.$store.getters["planos.get"].filter((obj)=>obj.company_id==this.id_empresa || this.showAll);
      if (this.aba.tipo == "checklists") return this.$store.getters["checklists.get"].filter((obj)=>obj.company_id==this.id_empresa || this.showAll);
      return []
    },
    aba() {
      const index = this.$store.getters["abas.getIndices"][this.$route.path];
      const abas = this.$store.getters["abas.getAbas"][this.$route.path];
      return index != undefined ? abas[index] : {};
    },
    id_empresa(){
			return this.$store.getters['empresa.get'].id
		}
  },
  watch: {
    aba() {
      this.loadData();
    },
  },
  methods: {
    add() {
      this.$store.dispatch("edit-modal.editShow", {
        objeto: this.aba.model,
        callback: this.aba.callback,
      });
    },
    openCalendar() {
      this.$store.dispatch("servicos.calendar");
    },
    refresh() {
      this.loadData();
    },
    loadData() {
      if (this.aba && this.aba.callback) this.$store.dispatch(this.aba.callback);
    },
  },
  mounted() {
    this.loadData();
    this.$store.dispatch("categorias.load", "ativos");
    this.$store.dispatch("categorias.load", "maintenance");
    this.$store.dispatch("setores.load");
    this.$store.dispatch("ativos.load");
    this.$store.dispatch("mantenedores.load");
    this.$store.dispatch("projetos.load");
    this.$store.dispatch("planos.load");
    this.$store.dispatch("membros.load");
  },
};
</script>

<style scoped>
.system-patrimonio {
  margin: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /*display: grid;*/
  /*grid-template-rows: 150px 1fr;*/
  /*grid-template-areas:
    "estatistica"
    "patrimonio-tabela";*/
}
.ativos-filter {
  margin-bottom: 10px;
  width: 100%;
  background-color: var(--c5) !important;
}

.ativos-filter-input {
  color: white !important;
}

.red {
  color: red;
}
</style>
