import Ativo from '@/models/ativo'
import { url } from '@/configs/global'

export default {
    state: () => ({
        ativos: [],
        current_page: 1,
        last_page: 1
    }),
    mutations: {
        'ativos.set': function(state, {ativos, current_page, last_page}){
            if(ativos != undefined) state.ativos = ativos
            if(current_page != undefined) state.current_page = current_page
            if(last_page != undefined) state.last_page = last_page
        },
    },
    getters: {
        'ativos.get': function(state){
            return state.ativos
        },
        'ativos.getCurrentPage': function(state){
            return state.current_page
        },
        'ativos.getLastPage': function(state){
            return state.last_page
        }
    },
    actions: {
        'ativos.load': function( { dispatch, getters, commit}, page){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
			this.axios.get(`${url}/api/${id_e}/get_ativos_company?page=${page? page : 1}`)
			.then((response) => {
                commit('ativos.set', {
                    current_page: response.data.data.current_page,
                    last_page: response.data.data.last_page,
                    ativos: response.data.data.data.map(obj => new Ativo(obj, this.axios)).sort((a, b) => a.description.localeCompare(b.description))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        }
    }
}