import Report from '@/models/report'
import { url } from '@/configs/global'

export default {
    state: () => ({
        reports: [],
        current_page: 1,
        last_page: 1
    }),
    mutations: {
        'reports.set': function(state, {reports, current_page, last_page}){
            if(reports != undefined) state.reports = reports
            if(current_page != undefined) state.current_page = current_page
            if(last_page != undefined) state.last_page = last_page
        },
    },
    getters: {
        'reports.get': function(state){
            return state.reports
        },
        'reports.getCurrentPage': function(state){
            return state.current_page
        },
        'reports.getLastPage': function(state){
            return state.last_page
        }
    },
    actions: {
        'reports.load': function( { dispatch, getters, commit}, page){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
			this.axios.get(`${url}/api/${id_e}/get_reports?page=${page? page : 1}`)
			.then((response) => {
                commit('reports.set', {
                    current_page: response.data.data.current_page,
                    last_page: response.data.data.last_page,
                    reports: response.data.data.data.map(obj => new Report(obj, this.axios)).sort((a, b) => a.anomaly.localeCompare(b.anomaly))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        }
    }
}