import { url, model } from '@/configs/global'

class ServicoCusto extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'servico_custo'
        this.allowRemove = true
        this.created_system = false

        this.listaCriacao = [
            {label: 'description', type: 'text', labelInput: 'Descrição'},
            {label: 'type', type: 'select', textBy: 'label', valueBy: 'label', trackBy: 'label', labelInput: 'Tipo',
            options: () => [{'label': 'Material'}, {'label': 'Mão de Obra'}, {'label': 'Diversos'}]},
            {label: 'value', type: 'text', labelInput: 'Valor'},
            {label: 'date', type: 'datetime', labelInput: 'Data', onlyDate: true},
        ]
        this.listaAtualizacao = this.listaCriacao
        
        this.concat = ['service_id']
    }

    async create({getters}){
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_service_custo`, obj)
    }

    async update({getters}){
        let obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/update_service_custo/${this.id}`, obj)
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_service_custo/${this.id}`)
    }
}

export default ServicoCusto