import { url, model } from '@/configs/global'

class ServicoComentario extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'servico_comentario'
        this.allowRemove = true
        this.created_system = false

        this.listaCriacao = [
            {label: 'description', type: 'text', labelInput: 'Descrição'},
        ]
        this.listaAtualizacao = this.listaCriacao
        
        this.concat = ['service_id', 'created_system']
    }

    async create({getters}){
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_service_note`, obj)
    }

    async update({getters}){
        let obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/update_service_note/${this.id}`, obj)
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_service_note/${this.id}`)
    }
}

export default ServicoComentario