import { url, model } from '@/configs/global'

class ServicoMaoDeObra extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'servico_maodeobra'
        this.allowRemove = true
        this.created_system = false

        this.listaCriacao = [
            {label: 'responsible_id', type: 'select', textBy: 'name', valueBy: 'id', trackBy: 'id', labelInput: 'Mantenedor responsável',
            options: ({getters}) => {
                return getters['mantenedores.get']
            }},
            {label: 'dt_start', type: 'datetime', labelInput: 'Início'},
            {label: 'dt_end', type: 'datetime', labelInput: 'Fim'},
        ]
        this.listaAtualizacao = this.listaCriacao
        
        this.concat = ['service_id']
    }

    async create({getters}){
        const obj = this.filterCreate()
        const id_e = getters['empresa.get'].id
        return await this.axios.post(`${url}/api/${id_e}/save_service_mao_obra`, obj)
    }

    async update({getters}){
        let obj = this.filterUpdate()
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/update_service_mao_obra/${this.id}`, obj)
    }

    async delete({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.delete(`${url}/api/${id_e}/delete_service_mao_obra/${this.id}`)
    }
}

export default ServicoMaoDeObra