<template>
	<div class="empresa-relatorio" >
		<div class="relatorio-item" v-for="(objeto, index) in objetos" :key="objeto.nome">
			<div @click="showdropdown(index)" class="relatorio-titulo">
				{{objeto.nome}}
				<va-button  v-if="show[index] == true" color="verde" @click="pdf(objeto.nome)"> Gerar PDF</va-button>
			</div>
			<div class="relatorio-dropdown" v-if="show[index] == true"> 
				<span v-if="objeto.nome == 'Inventário de Ativos'">
					<div class="relatorio-inputs">
						<span class="input-width"><va-input v-model="titulo" label="Título" color="cinza" placeholder="Ex: Inventário de Máquinas"/></span>
						<span class="input-width"><va-input v-model="numero" label="Número do documento" color="cinza" placeholder="Ex: CME-0033-22-IM001"/></span>
						
					</div>
					<div class="relatorio-inputs">
						<span class="input-width"><va-select searchable v-model="categoria" label="Categoria" color="cinza" :options="getCategoriasAtivos" text-by="description" trackBy='id' valueBy='id'/></span>
						<span class="input-width"><va-select searchable v-model="ativo" label="Ativo pai" color="cinza" :options="getAtivos" text-by="description" trackBy='id' valueBy='id'/></span>
					</div>
				</span>
				<span v-if="objeto.nome == 'Relatório de Serviços'">
					<div class="relatorio-inputs">
						<span class="input-width"><va-input v-model="titulo" label="Título" color="cinza" placeholder="Ex: Relatório Mensal de Atividades (RMA)"/></span>
						<span class="input-width"><va-input v-model="numero" label="Número do documento" color="cinza" placeholder="Ex: CME-0033-22-RMA001"/></span>
						
					</div>
					<div class="relatorio-inputs">
						<span class="input-width"><va-select searchable v-model="categoria" label="Tipo de Serviço" color="cinza" :options="getCategoriasServicos" text-by="description" trackBy='id' valueBy='id'/></span>
						<span class="input-width"><va-select searchable v-model="ativo" label="Ativo" color="cinza" :options="getAtivos" text-by="description" trackBy='id' valueBy='id'/></span>
					</div>
					<div class="relatorio-inputs">
						<span class="input-width"><va-date-input v-model="dtStart" label="Data inicial" color="cinza" /></span>
						<span class="input-width"><va-date-input v-model="dtEnd" label="Data final" color="cinza" /></span>
					</div>
				</span>
				<span v-if="objeto.nome == 'Relatório de Reports'">
					<div class="relatorio-inputs">
						<span class="input-width"><va-input v-model="titulo" label="Título" color="cinza" placeholder="Ex: Relatório de Reports Mensal (RMR)"/></span>
						<span class="input-width"><va-input v-model="numero" label="Número do documento" color="cinza" placeholder="Ex: CME-0033-22-RMR001"/></span>
						
					</div>
					<div class="relatorio-inputs">
						<span class="input-width"><va-select searchable v-model="criticidade" label="Criticidade" color="cinza" :options="getCriticidade"/></span>
						<span class="input-width"><va-select searchable v-model="ativo" label="Ativo" color="cinza" :options="getAtivos" text-by="description" trackBy='id' valueBy='id'/></span>
					</div>
					<div class="relatorio-inputs">
						<span class="input-width"><va-date-input v-model="dtStart" label="Data inicial" color="cinza" /></span>
						<span class="input-width"><va-date-input v-model="dtEnd" label="Data final" color="cinza" /></span>
					</div>
				</span>
			</div>
		</div>
	</div>	
</template>

<script>
//import {url} from '../../configs/global'
import { url_relatorio } from '@/configs/global'
export default {
	name: 'EmpresaRelatorios',
	props : ['objetos'],
	data () {
		return {
			show: [false, false ,false],
			titulo: '',
			numero: '',
			categoria: '',
			ativo: '',
			dtStart: new Date(),
			dtEnd: new Date(),
			criticidade: '',
			empresa: '',
    	}
	},
    computed: {
        getEmpresa(){
			return this.$store.getters['empresa.get']
		},
		getAtivos(){
			return this.$store.getters['ativos.get']
		},
		getCategoriasAtivos(){
			return this.$store.getters['categorias.get']['ativos']
		},
		getCategoriasServicos(){
			return this.$store.getters['categorias.get']['maintenance']
		},
		getCriticidade(){
			return ['Baixa', 'Média', 'Importante', 'Urgente']
		},
    },
	watch: {
		getEmpresa(value){
			this.empresa = value
		}
	},
	methods:{
		load(){
			this.empresa = this.getEmpresa
		},
		showdropdown(index){
			if (this.show[index]){
				this.show = [false, false ,false]
				}
			else{
				this.show = [false, false ,false]
				this.show[index] = true
			} 
		}, 
		pdf(tipo){
			if(tipo == 'Inventário de Ativos'){
				const url_download = `${url_relatorio}/api/${this.empresa.id}/relatorio_ativos?document_number=${this.numero}&title=${this.titulo}&category_id=${this.categoria}&parent_id=${this.ativo}`
				//console.log(url_download)
				window.open(url_download, "_blank");
			}
			if(tipo == 'Relatório de Serviços'){
				const dtStart = this.dtStart.getFullYear() + '-' + (this.dtStart.getMonth() + 1)  + '-' + this.dtStart.getDate()
				const dtEnd = this.dtEnd.getFullYear() + '-' + (this.dtEnd.getMonth() + 1)  + '-' + this.dtEnd.getDate()
				const url_download = `${url_relatorio}/api/${this.empresa.id}/relatorio_servicos?document_number=${this.numero}&title=${this.titulo}&maintenance_type_id=${this.categoria}&ativo_id=${this.ativo}&dtStart=${dtStart}&dtEnd=${dtEnd}`
				//console.log(url_download)
				window.open(url_download, "_blank");
			}
			if(tipo == 'Relatório de Reports'){
				const dtStart = this.dtStart.getFullYear() + '-' + (this.dtStart.getMonth() + 1)  + '-' + this.dtStart.getDate()
				const dtEnd = this.dtEnd.getFullYear() + '-' + (this.dtEnd.getMonth() + 1)  + '-' + this.dtEnd.getDate()
				const url_download = `${url_relatorio}/api/${this.empresa.id}/relatorio_reports?document_number=${this.numero}&title=${this.titulo}&criticality=${this.criticidade}&ativo_id=${this.ativo}&dtStart=${dtStart}&dtEnd=${dtEnd}`
				//console.log(url_download)
				window.open(url_download, "_blank");
			}
			this.titulo = ''
			this.numero = ''
			this.categoria = ''
			this.ativo = ''
			this.dtStart = new Date()
			this.dtEnd = new Date()
			this.criticidade = ''
		}
	},
	mounted() {
		this.load()
	},
}
</script>

<style scoped>

.empresa-relatorio {
	margin-bottom: 10px;
	margin-top: 10px;
}
.relatorio-item {
	/*padding: 10px;*/
	margin-bottom: 10px;
	background-color: var(--c1);
	color: var(--branco)
}

.relatorio-item:hover{
	background-color: #444753;
	cursor: pointer;
}

.relatorio-dropdown{
	padding: 10px;
	width: 70%;
	display: flex;
	flex-direction: column;
	justify-content:space-between;
} 
.relatorio-inputs{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.input-width{
	width: 350px;
	padding: 2px;;
}
.relatorio-titulo{
	padding: 10px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background-color: #3C3F4A99;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.container:hover .overlay {
  opacity: 1;
}
</style>