<template>
   <div class="estatistica">
	   <div class="estatistica-cards">
			<EstatisticaCard v-for="categoria in getCategorias(objetos, aba.nome)" :key="categoria" :nome="categoria" :numero="getNumero(objetos, aba.nome, categoria)" />
	   </div>
	   <!--<div class="estatistica-ilustracao">
		    <div class="estatistica-caption">{{caption}}</div>
	   		<img v-if="img != ''" :src="require('@/assets/'+img)" alt="" class="estatistica-img">
	   </div>-->
   </div>
</template>

<script>
import EstatisticaCard from './EstatisticaCard'
export default {
    name: 'Estatistica',
	props : ['img', 'aba', 'objetos', 'caption'],
	components:{EstatisticaCard},
	computed: {
	},
	methods:{
		getCategorias(obj, aba){
			let CATEGORIAS = [];
			if(aba == 'Ativos'){
				for (let i = 0; i < obj.length; i++) {
					if(!CATEGORIAS.includes(obj[i].category.description)) CATEGORIAS=CATEGORIAS.concat(obj[i].category.description);
				}
				CATEGORIAS = CATEGORIAS.sort()
			}
			else if(aba == 'Reports'){
				let todas = []
				for (let i = 0; i < obj.length; i++) todas = todas.concat(obj[i].criticality)
				if (todas.includes('Urgente')) CATEGORIAS = CATEGORIAS.concat('Urgente')
				if (todas.includes('Importante')) CATEGORIAS = CATEGORIAS.concat('Importante')
				if (todas.includes('Média')) CATEGORIAS = CATEGORIAS.concat('Média')
				if (todas.includes('Baixa')) CATEGORIAS = CATEGORIAS.concat('Baixa')
				/*for (let i = 0; i < obj.length; i++) {
					if(!CATEGORIAS.includes(obj[i].criticality)) CATEGORIAS=CATEGORIAS.concat(obj[i].criticality);
				}*/
			}
			else if(aba == 'Ordens de Serviço'){
				CATEGORIAS = ['Concluídos', 'Em aberto']
			}
			else if(aba == 'Mantenedores'){
				CATEGORIAS= ['Interno', 'Externo/Convidado']
			}
			return CATEGORIAS
		},
		getNumero(obj, aba, categoria){
			let NUMERO = 0;
			if(aba == 'Ativos'){
				for (let i = 0; i < obj.length; i++) {
					if(obj[i].category.description == categoria) NUMERO = NUMERO + 1
				}
			}
			else if(aba == 'Reports'){
				for (let i = 0; i < obj.length; i++) {
					if(obj[i].criticality == categoria) NUMERO = NUMERO + 1
				}
			}
			else if(aba == 'Ordens de Serviço'){
				for (let i = 0; i < obj.length; i++) {
					if(categoria == 'Concluídos'){
						if(obj[i].finish) NUMERO = NUMERO + 1
					}
					else{
						if(!obj[i].finish) NUMERO = NUMERO + 1
					}
				}
			}
			else if(aba == 'Mantenedores'){
				console.log("estou aqui")
				if(categoria == 'Interno'){
					for (let i = 0; i < obj.length; i++) {
						if(obj[i].source == 0) NUMERO = NUMERO + 1
					}
				}
				else if(categoria == 'Externo/Convidado'){
					for (let i = 0; i < obj.length; i++) {
						if(obj[i].source == 1) NUMERO = NUMERO + 1
					}
				}
			}
			return NUMERO
		}
	}
}
</script>

<style scoped>
.estatistica {
	margin-top: 10px;
	/*grid-area: estatistica;*/
	background-color: var(--c1);
	display: flex;
	flex-direction: row;
}

.estatistica-cards{
	width: 100%;
	display: flex;
	flex-direction: row;
	/*align-items: center;*/
	flex-wrap: wrap;
	padding: 5px;
}

.estatistica-ilustracao{
	width: 35%;
	height: 100px;
	display: flex;
	flex-direction: row;
}

.estatistica-img{
	height: 75%;
	margin: 20px;
	/*scale: 10%;*/
}
.estatistica-caption{
	width: 40%;
	margin: 10px;
	color: var(--c3);
	align-self: center;
	font-size: 1.2em;
	/*scale: 10%;*/
}
</style>